import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { FaFilter } from "react-icons/fa";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import trainingProgramApi from "api/common/trainingProgram";
import { toast } from "react-toastify";

export default function TableFilter({
    currentFilter,
    handleFilterOnClick,
    handleTrainingProgramFilterOnChange,
}) {
    const { t } = useTranslation();
    const [listTrainingProgram, setlistTrainingProgram] = useState([]);

    useEffect(async () => {
        try {
            const listTrainingProgramRes = await trainingProgramApi.getAll();
            if (listTrainingProgramRes) {
                setlistTrainingProgram(listTrainingProgramRes.contents);
            }
        } catch (err) {
            setlistTrainingProgram([])
        }
    }, []);

    return (
        <Form className="d-flex flex-wrap">
            <FormGroup>
                <Label for="chuongTrinhDaoTao">
                    {t("admin.training_major_list.PROGRAM")}
                </Label>
                <Input
                    type="select"
                    value={
                        currentFilter.chuongTrinhDaoTao !== null
                            ? currentFilter.chuongTrinhDaoTao
                            : -1
                    }
                    onChange={handleTrainingProgramFilterOnChange}
                >
                    <option value={-1}>{t("admin.common.ALL")}</option>
                    {listTrainingProgram &&
                        listTrainingProgram.map((item, index) => {
                            return (
                                <option key={index + 1} value={item.id}>
                                    {/* {t("admin.training_major_list.PROGRAM_CODE")}:{" "} */}
                                    {item?.maCTDT}&ensp;
                                    {"/"}&ensp;
                                    {/* {t("admin.training_major_list.PROGRAM_NAME")}:{" "} */}
                                    {item?.ten}
                                </option>
                            );
                        })}
                </Input>
            </FormGroup>
            &emsp;
            <FormGroup className="align-self-end">
                <Button onClick={handleFilterOnClick}>
                    <FaFilter /> {t("admin.common.FILTER")}
                </Button>
            </FormGroup>
        </Form>
    );
}
